import { Component, OnInit } from '@angular/core';
import { Student } from '../common/student.model';
import { Router } from "@angular/router";
import * as Highcharts from 'highcharts';
import { DataService } from '../utility/data.service';
import { DashboardApiService } from '../common/dashboard-api.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  allStudentsData: Student[] = [];
  allFailStudents: Student[] = [];
  studentWarningCount: number = 0;
  studentCriticalCount: number = 0;
  allPassStudents: Student[] = [];
  studentDistinctionCount: number = 0;
  studentPassCount: number = 0;
  public failDonutOptions: any = {
    chart: {
      backgroundColor: 'transparent',
      margin: [0, 0, 0, 0],
      spacingTop: 0,
                spacingBottom: 0,
                spacingLeft: 0,
                spacingRight: 0,
    },
    colors:["#DD3131","#E29619"],
    credits: {
      enabled: false
    },
    title: {
      text: "",
      align: 'center',
      verticalAlign: 'middle',
      y: 0,
     style: {'color':'#3B6475', 'fontSize': '24px', 'font-family': 'OpenSans-Regular'}
    },
    tooltip: {
      enabled: false,
    },
    plotOptions: {
      pie: {
        states: {
          hover: {
            enabled: false
          },
          inactive: {
            enabled: false
          }
        },
        dataLabels: {
          enabled: false,
          // "softConnector": false, "y": 0,
          // 'distance': 10,
        },
        center: ['50%', '40%'],
        size: '70%'
      }
    },
    series: [{
      type: 'pie',
      innerSize: '80%',
      data: [0, 0]
    }]
  };
  public passDonutOptions: any = {
    chart: {
      backgroundColor: 'transparent',
      margin: [0, 0, 0, 0],
      spacingTop: 0,
                spacingBottom: 0,
                spacingLeft: 0,
                spacingRight: 0,
    },
    colors:["#009CDB","#78B5D9"],
    credits: {
      enabled: false
    },
    title: {
      text: "",
      align: 'center',
      verticalAlign: 'middle',
      y: 0,
     style: {'color':'#3B6475', 'fontSize': '24px', 'font-family': 'OpenSans-Regular'}
    },
    tooltip: {
      enabled: false,
    },
    plotOptions: {
      pie: {
        states: {
          hover: {
            enabled: false
          },
          inactive: {
            enabled: false
          }
        },
        dataLabels: {
          enabled: false,
          // "softConnector": false, "y": 0,
          // 'distance': 10,
        },
        center: ['50%', '40%'],
        size: '70%'
      }
    },
    series: [{
      type: 'pie',
      innerSize: '80%',
      data: [0, 0]
    }]
  };
  constructor(private apiService: DashboardApiService, private dataService: DataService, private router: Router) {

  }

  ngOnInit() {
    this.apiService.getAllStudents().subscribe(data => {
      this.allStudentsData = data.students;
      this.dataService.allStudentsData$.next(this.allStudentsData);
      this.getFailStudent();
      this.getLowAttendance();
    })
  }

  getFailStudent() {
    this.allStudentsData.map(student => {
      let counter = 0;
      for (let property in student.marks) {
        if (student.marks.hasOwnProperty(property)) {
          if (student.marks[property] < 35) {
            counter++;
          }
        }
      }
      if (counter === 1) {
        student.severity = "Warning";
        this.studentWarningCount++;
        this.allFailStudents.push(student);
      } else if (counter > 1) {
        student.severity = "Critical";
        this.studentCriticalCount++;
        this.allFailStudents.push(student);
      }
    });
    this.failDonutOptions.title.text = this.allFailStudents.length;
    this.failDonutOptions.series[0].data = [this.studentCriticalCount,this.studentWarningCount]
    Highcharts.chart('fail-chart-container', this.failDonutOptions);
    this.dataService.allFailStudents$.next(this.allFailStudents);
    this.getPassStudent();
  }

  getPassStudent() {
    let allUniquePassStudent: Student[] = this.allStudentsData.filter((obj) => {
      return !this.allFailStudents.some((obj2) => {
        return obj.id == obj2.id;
      });
    });
    allUniquePassStudent.map(passStudent => {
      let total = 0;
      for (let subject in passStudent.marks) {
        if (passStudent.marks.hasOwnProperty(subject)) {
          total += passStudent.marks[subject];
        }
      }
      if ((total / Object.keys(passStudent.marks).length) > 75) {
        passStudent.severity = "Distinction";
        this.studentDistinctionCount++;
      } else {
        passStudent.severity = "Pass";
        this.studentPassCount++;
      }
    });
    this.allPassStudents = [...allUniquePassStudent];
    this.dataService.allPassStudents$.next(this.allPassStudents);
    this.passDonutOptions.title.text = this.allPassStudents.length;
    this.passDonutOptions.series[0].data = [this.studentDistinctionCount,this.studentPassCount]
    Highcharts.chart('pass-chart-container', this.passDonutOptions);
  }

  getLowAttendance() {
    this.allStudentsData.map(student => {
      let attendence = (Object.keys(student.attendance).map(key => Number(student.attendance[key].replace("%", ""))).reduce((previous, current) => previous + current) / Object.keys(student.attendance).length);
      if (attendence < 60) {
        student["lowAttendance"] = String(attendence);
      }
    })
  }

  navigateToUrl(path: String) {
    let url = `/dashboard/${path}`
    this.router.navigateByUrl(url);
  }

}
