import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { AllStudentsModel } from "./student.model";
import { HttpClient } from '@angular/common/http';


@Injectable()
export class DashboardApiService {
    constructor(private http: HttpClient) {
    }

    private readonly getAllStudentUrl = `https://my-json-server.typicode.com/uxrtest/demo/db`;

    getAllStudents(): Observable<AllStudentsModel> {
        const fullUrl = `${this.getAllStudentUrl}`;
        return this.http.get(fullUrl).pipe(map((response) => {
            return response as AllStudentsModel
        }))

    }

}