import { Component, OnInit } from '@angular/core';
import { Student } from '../common/student.model';
import { DataService } from '../utility/data.service';

@Component({
  selector: 'app-fail-student',
  templateUrl: './fail-student.component.html',
  styleUrls: ['./fail-student.component.css']
})
export class FailStudentComponent implements OnInit {

  constructor(private dataService: DataService) { }
  failStudents: Student[] = [];
  failStudentsSearchResults: Student[] = [];

  ngOnInit() {
    this.dataService.allFailStudents$.subscribe(data => {
      this.failStudents = data;
      this.failStudentsSearchResults = data;
    });
  }
  search(queryString) {
    this.failStudentsSearchResults = this.failStudents.filter((obj) => {
      return obj.id == queryString || obj.name.toLowerCase().includes(queryString.toLowerCase());
    });
  }

}
