import { Component, OnInit } from '@angular/core';
import { Student } from '../common/student.model';
import { DataService } from '../utility/data.service';

@Component({
  selector: 'app-all-student',
  templateUrl: './all-student.component.html',
  styleUrls: ['./all-student.component.css']
})
export class AllStudentComponent implements OnInit {
  constructor(private dataService: DataService) { }
  failStudents: Student[] = [];
  failStudentsSearchResults: Student[] = [];
  passStudents: Student[] = [];
  passStudentsSearchResults: Student[] = [];

  ngOnInit() {
    this.dataService.allFailStudents$.subscribe(data => {
      this.failStudents = data;
      this.failStudentsSearchResults = data;
    });
    this.dataService.allPassStudents$.subscribe(data => {
      this.passStudents = data;
      this.passStudentsSearchResults = data;
    })
  }

  search(record, queryString) {
    if (record === "failStudents") {
      this.failStudentsSearchResults = this.failStudents.filter((obj) => {
        return obj.id == queryString || obj.name.toLowerCase().includes(queryString.toLowerCase());
      });
    } else {
      this.passStudentsSearchResults = this.passStudents.filter((obj) => {
        return obj.id == queryString || obj.name.toLowerCase().includes(queryString.toLowerCase());
      });
    }
  }

}
