import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AllStudentComponent } from './all-student/all-student.component';
import { FailStudentComponent } from './fail-student/fail-student.component';
import { PassStudentComponent } from './pass-student/pass-student.component';

const routes: Routes = [
    {
        path: 'dashboard', component: DashboardComponent, children: [
            { path: '', pathMatch: 'full', component: AllStudentComponent },
            { path: 'passtudent', component: PassStudentComponent },
            { path: 'failstudent', component: FailStudentComponent },
            { path: 'allstudent', component: AllStudentComponent },
        ]
    },
    { path: '**', redirectTo: 'dashboard' },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DashboardRoutingModule { }
